import React, { Suspense, lazy, useEffect } from 'react'

const Activities = lazy(()=> import('./Activities'));
const Destinations = lazy(()=> import('./Destinations'));
const Subscribe = lazy(()=> import('./Subscribe'));
const Packages = lazy(()=> import('./Packages'));
const Testimonials = lazy(()=> import('./Testimonials'));
const PlanTourInfo = lazy(()=> import('./PlanTourInfo'));
const Services = lazy(()=> import('./Services'));
const BlogsView = lazy(()=> import('./BlogsView'));


let loading = (
    <div className="loader-container">
      <div className="loader">
      
      </div>
    </div>
  )

function changeTitle(title){
    document.title = title;
}

function LayoutWrapper() {

    useEffect(()=>{
        changeTitle("Travel raasta | Best Deals in Hotel, Flights, Events, Bus, Tour , Guide , Tour Packages")
    },[])

    return (
        <div>
            <Suspense fallback={loading}>
                <Activities />
                <Destinations />
                <Subscribe />
                <Packages />
                <Testimonials />
                <PlanTourInfo />
                <Services />
                <BlogsView />
            </Suspense>
        </div>
    )
}


export default LayoutWrapper
