import React, { useEffect } from 'react'
import M from 'materialize-css/dist/js/materialize.min.js'
import { withRouter, Link } from 'react-router-dom'

function Navbar() {

    useEffect(()=>{
        var sideNav = document.querySelectorAll('.sidenav');
        M.Sidenav.init(sideNav, {});        

        let destinationDropDown = document.querySelectorAll('.dropdown-trigger');
        M.Dropdown.init(destinationDropDown, {
            hover: true,
            coverTrigger: false,
            constrainWidth: false
        });

    })

    return (
        <div>
            <div className="navbar-fixed">
                <nav className="nav-wrapper grey lighten-5">
                    <div className="container">
                        <a href="/" className="brand-logo left" >
                            <img src="/images/main_logo.png" alt="Logo" />
                        </a>
                        <a data-target="mobile-demo" className="sidenav-trigger right blue-text"><i className="material-icons">menu</i></a>
                        <ul className="right hide-on-med-and-down">
                            <li><Link to="/">Home</Link></li> 
                            <li><a className="dropdown-trigger valign-wrapper"  data-target="destinationdropdown">Destinations<i className="material-icons right drop-icon">arrow_drop_down</i></a></li> 
                            <li><a href="https://blogs.travelraasta.com/" target="_blank">Blogs</a></li>
                            <li><a href="/about">About</a></li>
                            <li><a href="/contact">Contact</a></li>
                            <li><a href="tel: +919818040401" className="valign-wrapper"><i className="material-icons left call-icon">call</i>+91 9818040401</a></li>
                        </ul>
                    </div>
                </nav>
            </div>

            <div id="destinationdropdown" className="dropdown-content row">
                <div className="col sm-6 left">
                    <h6 className="black-text">Indian Destinations</h6> 
                    <div className="row">
                        <div className="col sm-4 left">
                            <a href="/packages/ladakh">Ladakh</a>
                            <a href="/packages/mumbai">Mumbai</a>
                            <a href="/packages/varanasi">Varanasi</a>
                        </div>
                        <div className="col sm-6 right">
                            <a href="/packages/rajasthan">Rajasthan</a>
                            <a href="/packages/all" className="red-text">View all</a>
                        </div>
                    </div> 
                </div>
                <div className="col sm-5">
                    <h6 className="black-text">International Destinations</h6>  
                    <div className="row">
                        <div className="col sm-4 left">
                            <a href="/packages/dubai">Dubai</a>
                            <a href="/packages/australia">Australia</a>
                            <a href="/packages/all" className="red-text">View all</a>
                        </div>
                        <div className="col sm-4 right">
                            <a href="/packages/europe">Europe</a>
                        </div>
                    </div>
                </div>           

            </div>

            <ul className="sidenav collection with-header" id="mobile-demo">
                <li className="collection-header center">
                    <img src="/images/secondary_logo.png" id="sidenav-image" alt="second logo" />
                    <h5>!! Hi, There !!</h5>
                    <a href="tel: +919818040401" className="valign-wrapper">( +91 9818040401 )</a>
                </li>
                <li className="collection-item"><Link to="/">Home</Link></li>
                <li className="collection-item"><a href="/packages/all">Destination</a></li>
                <li className="collection-item"><a href="/about">About</a></li>
                <li className="collection-item"><a href="https://www.travelraasta.in/contact">Contact</a></li>
                <li className="collection-item"><a href="https://blogs.travelraasta.com/" target="_blank">Blogs</a></li>
            </ul>            
        </div>
    )
}

export default withRouter(Navbar)
