import React, { Suspense, lazy } from 'react';
import Navbar from './components/layout/Navbar'
import FooterView from './components/layout/Footer'; 
import { BrowserRouter, Route, Switch } from 'react-router-dom'
import LayoutWrapper from './components/layout/LayoutWrapper'

const DefaultCarousel = lazy(()=> import('./components/layout/DefaultCarousel'))
const FlightCaro = lazy(()=> import('./components/services/FlightCaro'))
const TourCaro = lazy(()=> import('./components/services/TourCaro'))
const HotelCaro = lazy(()=> import('./components/services/HotelCaro'))
const EventCaro = lazy(()=> import('./components/services/EventCaro'))
const GuideCaro = lazy(()=> import('./components/services/GuideCaro'))
const BusCaro = lazy(()=> import('./components/services/BusCaro'))
const About = lazy(()=> import('./components/layout/About'))
const PackagesWrapper = lazy(()=> import('./components/packages/PackagesWrapper'))
const PackageWrapper = lazy(()=> import('./components/package/PackageWrapper'))
const AllPackages = lazy(()=> import('./components/packages/AllPackages'))
const ContactUs = lazy(()=> import('./components/layout/ContactUs'))
const PayNow = lazy(()=> import('./components/layout/PayNow'))
// const FooterView = lazy(()=> import('./components/layout/Footer'))

let loading = (
  <div className="loader-container">
    <div className="loader">
    
    </div>
  </div>
)

function App() {
  console.log('Packages Branch')
  return (
    <BrowserRouter>
      <div className="App">
        <Navbar />
        <Suspense fallback={loading}>
          <Switch>
            <Route exact path="/" component={DefaultCarousel} />
            <Route path="/flights" component={FlightCaro} />
            <Route path="/tours" component={TourCaro} />
            <Route path="/hotels" component={HotelCaro} />
            <Route path="/guide" component={GuideCaro} />
            <Route path="/events" component={EventCaro} />
            <Route path="/bus" component={BusCaro} />
          </Switch>
        </Suspense>

        <Suspense fallback={loading}>
          <Switch>
            <Route path="/pay"  component={PayNow} />
            <Route exact path="/packages/all" component={AllPackages} />
            <Route path="/package/:packagesId/:packageName" component={PackageWrapper} />
            <Route path="/packages/:packageId" component={PackagesWrapper} />
            <Route path="/contact"  component={ContactUs} /> 
            <Route path="/about" component={About} />
            <Route path="/" component={LayoutWrapper} />
          </Switch>
        </Suspense>

        <FooterView />
      </div>
    </BrowserRouter>
  );
}

export default App;
